___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input,
button,
textarea,
select {
  margin: 0;
  font: inherit;
}

:root {
  --font-family-main: sans-serif, "Times New Roman", serif;
  --font-size-m: 16px;
  --font-line-m: 24px;
  --font-m: var(--font-size-m) / var(--font-line-m) var(--font-family-main);
  --font-size-l: 24px;
  --font-line-l: 32px;
  --font-l: var(--font-size-l) / var(--font-line-l) var(--font-family-main);
  --font-size-xl: 32px;
  --font-line-xl: 40px;
  --font-xl: var(--font-size-xl) / var(--font-line-xl) var(--font-family-main);
  --navbar-height: 50px;
  --sidebar-width: 300px;
  --sidebar-width-collapsed: 80px;
  --modal-z-index: 10;
  --overlay-color: rgba(0 0 0 / 60%);
  --red-light: #f00;
  --red-dark: #ce0505;
}

.dark {
  --bg-color: #090949;
  --inverted-bg-color: #e8e8ea;
  --primary-color: #04ff04;
  --secondary-color: #049604;
  --inverted-primary-color: #3aa192;
  --inverted-secondary-color: #0452ff;
}

.light {
  --bg-color: #e8e8ea;
  --inverted-bg-color: #090949;
  --primary-color: #3aa192;
  --secondary-color: #0452ff;
  --inverted-primary-color: #04ff04;
  --inverted-secondary-color: #049604;
}

.app {
  font: var(--font-m);
  color: var(--primary-color);
  background: var(--bg-color);
  min-height: 100vh;
}

.contentPage {
  display: flex;
}

.pageWrapper {
  flex-grow: 1;
  padding: 20px;
}`, "",{"version":3,"sources":["webpack://./src/app/styles/reset.scss","webpack://./src/app/styles/index.scss","webpack://./src/app/styles/variables/global.scss","webpack://./src/app/styles/themes/dark.scss","webpack://./src/app/styles/themes/light.scss"],"names":[],"mappings":"AAAA;EACI,SAAA;EACA,UAAA;EACA,sBAAA;ACCJ;;ADEA;;;;EAII,SAAA;EACA,aAAA;ACCJ;;ACZA;EACI,wDAAA;EACA,mBAAA;EACA,mBAAA;EACA,yEAAA;EACA,mBAAA;EACA,mBAAA;EACA,yEAAA;EACA,oBAAA;EACA,oBAAA;EACA,4EAAA;EACA,qBAAA;EACA,sBAAA;EACA,+BAAA;EACA,mBAAA;EACA,kCAAA;EACA,iBAAA;EACA,mBAAA;ADeJ;;AEhCA;EACI,mBAAA;EACA,4BAAA;EACA,wBAAA;EACA,0BAAA;EACA,iCAAA;EACA,mCAAA;AFmCJ;;AGzCA;EACI,mBAAA;EACA,4BAAA;EACA,wBAAA;EACA,0BAAA;EACA,iCAAA;EACA,mCAAA;AH4CJ;;AA7CA;EACI,mBAAA;EACA,2BAAA;EACA,2BAAA;EACA,iBAAA;AAgDJ;;AA7CA;EACI,aAAA;AAgDJ;;AA7CA;EACI,YAAA;EACA,aAAA;AAgDJ","sourcesContent":["* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\ninput, \nbutton, \ntextarea, \nselect {\n    margin: 0;\n    font: inherit;\n}","@import 'reset';\n@import 'variables/global';\n@import 'themes/dark';\n@import 'themes/light';\n\n.app {\n    font: var(--font-m);\n    color: var(--primary-color);\n    background: var(--bg-color);\n    min-height: 100vh;\n}\n\n.contentPage {\n    display: flex;\n}\n\n.pageWrapper {\n    flex-grow: 1;\n    padding: 20px;\n}",":root {\n    --font-family-main: sans-serif, \"Times New Roman\", serif;   \n    --font-size-m: 16px;\n    --font-line-m: 24px;\n    --font-m: var(--font-size-m) / var(--font-line-m) var(--font-family-main);\n    --font-size-l: 24px;\n    --font-line-l: 32px;\n    --font-l: var(--font-size-l) / var(--font-line-l) var(--font-family-main);\n    --font-size-xl: 32px;\n    --font-line-xl: 40px;\n    --font-xl: var(--font-size-xl) / var(--font-line-xl) var(--font-family-main);\n    --navbar-height: 50px;\n    --sidebar-width: 300px;\n    --sidebar-width-collapsed: 80px;\n    --modal-z-index: 10;\n    --overlay-color: rgba(0 0 0 / 60%);\n    --red-light: #f00;\n    --red-d